import React from "react";
import _ from "lodash";

const Pagination = (props) => {
  const { itemsCount, pageSize, currentPage, onPageChange } = props; // ê°ê° ì•„ì´í…œ(ì˜í™”ëª©ë¡) ê°œìˆ˜, í•œ í˜ì´ì§€ì— ë³´ì—¬ì¤„ ì•„ì´í…œ(ì˜í™”ëª©ë¡) ê°œìˆ˜
  const pageCount = Math.ceil(itemsCount / pageSize); // ëª‡ í˜ì´ì§€ê°€ í•„ìš”í•œì§€ ê³„ì‚°

  const pages = _.range(1, pageCount + 1); // ë§ˆì§€ë§‰ í˜ì´ì§€ì— ë³´ì—¬ì¤„ ì»¨í…ì¸ ë¥¼ ìœ„í•´ +1, https://lodash.com/docs/#range ì°¸ê³ 
  return (
    <nav>
      <ul className="pagination">
        {pages.map((page) => (
          <li
            key={page}
            className={page === currentPage ? "page-item active" : "page-item"} // Bootstrapì„ ì´ìš©í•˜ì—¬ í˜„ì¬ í˜ì´ì§€ë¥¼ ì‹œê°ì ìœ¼ë¡œ í‘œì‹œ
            style={{ cursor: "pointer" }}
          >
            <div className="page-link" onClick={() => onPageChange(page)}>
              {page}
            </div>
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default Pagination;
